module.exports = "import React from \"react\";\nimport { StyleSheet, Text, View } from \"react-native\";\n\nimport { NativeRouter, Route, Link } from \"react-router-native\";\n\nfunction CustomLink({ children, to, activeOnlyWhenExact }) {\n  return (\n    <Route\n      path={to}\n      exact={activeOnlyWhenExact}\n      children={({ match }) => (\n        <Link style={styles.navItem} underlayColor=\"#f0f4f7\" to={to}>\n          <View style={{ flexDirection: \"row\" }}>\n            {match && <Text>&gt;</Text>}\n            {children}\n          </View>\n        </Link>\n      )}\n    />\n  );\n}\n\nfunction App() {\n  return (\n    <NativeRouter>\n      <View style={styles.container}>\n        <Text style={styles.header}>Accounts</Text>\n        <View style={styles.nav}>\n          <CustomLink to=\"/netflix\">\n            <Text>Netflix</Text>\n          </CustomLink>\n          <CustomLink to=\"/zillow-group\">\n            <Text>Zillow Group</Text>\n          </CustomLink>\n          <CustomLink to=\"/yahoo\">\n            <Text>Yahoo</Text>\n          </CustomLink>\n          <CustomLink to=\"/modus-create\">\n            <Text>Modus Create</Text>\n          </CustomLink>\n        </View>\n        <Route\n          path=\"/:id\"\n          render={({ match }) => <Text>ID: {match.params.id}</Text>}\n        />\n      </View>\n    </NativeRouter>\n  );\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    marginTop: 25,\n    padding: 10\n  },\n  header: {\n    fontSize: 20\n  },\n  nav: {\n    flexDirection: \"row\",\n    justifyContent: \"space-around\"\n  },\n  navItem: {\n    flex: 1,\n    alignItems: \"center\",\n    padding: 10\n  }\n});\n\nexport default App;\n"