module.exports = "import React from \"react\";\nimport {\n  StyleSheet,\n  Text,\n  View,\n  TextInput,\n  TouchableOpacity\n} from \"react-native\";\n\nimport { NativeRouter, Route, Link, Prompt } from \"react-router-native\";\n\nclass Form extends React.Component {\n  state = { isBlocking: false, text: \"\" };\n\n  handleClick = event => {\n    event.preventDefault();\n    this.setState({\n      isBlocking: false,\n      text: \"\"\n    });\n  };\n\n  render() {\n    const { isBlocking } = this.state;\n\n    return (\n      <View>\n        <Prompt\n          when={isBlocking}\n          message={location =>\n            `Are you sure you want to go to ${location.pathname}`\n          }\n        />\n\n        <Text>\n          Blocking?{\" \"}\n          {isBlocking ? \"Yes, click a link or the back button\" : \"Nope\"}\n        </Text>\n\n        <TextInput\n          value={this.state.text}\n          style={{ height: 40, borderWidth: 1, borderColor: \"black\" }}\n          placeholder=\"Type here to block transitions!\"\n          onChangeText={text => {\n            this.setState({\n              isBlocking: text.length > 0,\n              text\n            });\n          }}\n        />\n\n        <TouchableOpacity style={styles.btn} onPress={this.handleClick}>\n          <Text>Reset Blocking</Text>\n        </TouchableOpacity>\n      </View>\n    );\n  }\n}\n\nfunction About() {\n  return <Text style={styles.header}>About</Text>;\n}\n\nfunction Friends() {\n  return <Text style={styles.header}>Friends</Text>;\n}\n\nfunction App() {\n  return (\n    <NativeRouter>\n      <View style={styles.container}>\n        <View style={styles.nav}>\n          <Link to=\"/\" underlayColor=\"#f0f4f7\" style={styles.navItem}>\n            <Text>Home</Text>\n          </Link>\n          <Link to=\"/about\" underlayColor=\"#f0f4f7\" style={styles.navItem}>\n            <Text>About</Text>\n          </Link>\n          <Link to=\"/friends\" underlayColor=\"#f0f4f7\" style={styles.navItem}>\n            <Text>Friends</Text>\n          </Link>\n        </View>\n\n        <Route exact path=\"/\" component={Form} />\n        <Route path=\"/about\" component={About} />\n        <Route path=\"/friends\" component={Friends} />\n      </View>\n    </NativeRouter>\n  );\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    marginTop: 25,\n    padding: 10\n  },\n  header: {\n    fontSize: 20\n  },\n  nav: {\n    flexDirection: \"row\",\n    justifyContent: \"space-around\"\n  },\n  navItem: {\n    flex: 1,\n    alignItems: \"center\",\n    padding: 10\n  },\n  btn: {\n    width: 200,\n    backgroundColor: \"#E94949\",\n    justifyContent: \"center\",\n    alignItems: \"center\",\n    padding: 10,\n    marginTop: 10\n  }\n});\n\nexport default App;\n"