module.exports = "import React from \"react\";\nimport { StyleSheet, Text, View } from \"react-native\";\n\nimport { NativeRouter, Route, Link } from \"react-router-native\";\n\n// Some folks find value in a centralized route config.\n// A route config is just data. React is great at mapping\n// data into components, and <Route> is a component.\n\n////////////////////////////////////////////////////////////\n// first our route components\nfunction Sandwiches() {\n  return <Text style={styles.header}>Sandwiches</Text>;\n}\n\nfunction Tacos({ routes }) {\n  return (\n    <View>\n      <Text style={styles.header}>Tacos</Text>\n      <View style={styles.nav}>\n        <Link to=\"/tacos/bus\" style={styles.navItem} underlayColor=\"#f0f4f7\">\n          <Text>Bus</Text>\n        </Link>\n        <Link to=\"/tacos/cart\" style={styles.navItem} underlayColor=\"#f0f4f7\">\n          <Text>Cart</Text>\n        </Link>\n      </View>\n\n      {routes.map((route, i) => (\n        <RouteWithSubRoutes key={i} {...route} />\n      ))}\n    </View>\n  );\n}\n\nfunction Bus() {\n  return <Text style={styles.subHeader}>Bus</Text>;\n}\n\nfunction Cart() {\n  return <Text style={styles.subHeader}>Cart</Text>;\n}\n\n////////////////////////////////////////////////////////////\n// then our route config\nconst routes = [\n  {\n    path: \"/sandwiches\",\n    component: Sandwiches\n  },\n  {\n    path: \"/tacos\",\n    component: Tacos,\n    routes: [\n      {\n        path: \"/tacos/bus\",\n        component: Bus\n      },\n      {\n        path: \"/tacos/cart\",\n        component: Cart\n      }\n    ]\n  }\n];\n\n// wrap <Route> and use this everywhere instead, then when\n// sub routes are added to any route it'll work\nfunction RouteWithSubRoutes(route) {\n  return (\n    <Route\n      path={route.path}\n      render={props => (\n        // pass the sub-routes down to keep nesting\n        <route.component {...props} routes={route.routes} />\n      )}\n    />\n  );\n}\n\nfunction App() {\n  return (\n    <NativeRouter>\n      <View style={styles.container}>\n        <View style={styles.nav}>\n          <Link to=\"/tacos\" underlayColor=\"#f0f4f7\" style={styles.navItem}>\n            <Text>Tacos</Text>\n          </Link>\n          <Link to=\"/sandwiches\" underlayColor=\"#f0f4f7\" style={styles.navItem}>\n            <Text>Sandwiches</Text>\n          </Link>\n        </View>\n\n        {routes.map((route, i) => (\n          <RouteWithSubRoutes key={i} {...route} />\n        ))}\n      </View>\n    </NativeRouter>\n  );\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    marginTop: 25,\n    padding: 10\n  },\n  header: {\n    fontSize: 20\n  },\n  nav: {\n    flexDirection: \"row\",\n    justifyContent: \"space-around\"\n  },\n  navItem: {\n    flex: 1,\n    alignItems: \"center\",\n    padding: 10\n  },\n  subHeader: {\n    fontSize: 15\n  }\n});\n\nexport default App;\n"