module.exports = "import React from \"react\";\nimport { StyleSheet, Text, View } from \"react-native\";\n\nimport { NativeRouter, Route, Link } from \"react-router-native\";\n\n// Each logical \"route\" has two components, one for\n// the sidebar and one for the main area. We want to\n// render both of them in different places when the\n// path matches the current URL.\nconst routes = [\n  {\n    path: \"/\",\n    exact: true,\n    sidebar: () => <Text style={styles.sidebarText}>home!</Text>,\n    main: () => <Text style={styles.header}>Home</Text>\n  },\n  {\n    path: \"/bubblegum\",\n    sidebar: () => <Text style={styles.sidebarText}>bubblegum!</Text>,\n    main: () => <Text style={styles.header}>Bubblegum</Text>\n  },\n  {\n    path: \"/shoelaces\",\n    sidebar: () => <Text style={styles.sidebarText}>shoelaces!</Text>,\n    main: () => <Text style={styles.header}>Shoelaces</Text>\n  }\n];\n\nfunction App() {\n  return (\n    <NativeRouter>\n      <View style={styles.container}>\n        <View\n          style={{\n            backgroundColor: \"#f0f0f0\"\n          }}\n        >\n          <View>\n            <Link to=\"/\" underlayColor=\"#f0f4f7\">\n              <Text>Home</Text>\n            </Link>\n            <Link to=\"/bubblegum\" underlayColor=\"#f0f4f7\">\n              <Text>Bubblegum</Text>\n            </Link>\n            <Link to=\"/shoelaces\" underlayColor=\"#f0f4f7\">\n              <Text>Shoelaces</Text>\n            </Link>\n          </View>\n\n          {routes.map((route, index) => (\n            // You can render a <Route> in as many places\n            // as you want in your app. It will render along\n            // with any other <Route>s that also match the URL.\n            // So, a sidebar or breadcrumbs or anything else\n            // that requires you to render multiple things\n            // in multiple places at the same URL is nothing\n            // more than multiple <Route>s.\n            <Route\n              key={index}\n              path={route.path}\n              exact={route.exact}\n              component={route.sidebar}\n            />\n          ))}\n        </View>\n\n        <View>\n          {routes.map((route, index) => (\n            // Render more <Route>s with the same paths as\n            // above, but different components this time.\n            <Route\n              key={index}\n              path={route.path}\n              exact={route.exact}\n              component={route.main}\n            />\n          ))}\n        </View>\n      </View>\n    </NativeRouter>\n  );\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    marginTop: 25,\n    padding: 10\n  },\n  header: {\n    fontSize: 20,\n    marginTop: 20\n  },\n  sidebarText: {\n    fontWeight: \"bold\",\n    textAlign: \"center\"\n  }\n});\n\nexport default App;\n"