module.exports = "import React, { Component } from \"react\";\nimport { StyleSheet, Text, View, TouchableHighlight } from \"react-native\";\n\nimport {\n  NativeRouter,\n  Route,\n  Link,\n  Redirect,\n  withRouter\n} from \"react-router-native\";\n\nfunction AuthExample() {\n  return (\n    <NativeRouter>\n      <View style={styles.container}>\n        <AuthButton />\n        <View style={styles.nav}>\n          <Link to=\"/public\" style={styles.navItem} underlayColor=\"#f0f4f7\">\n            <Text>Public Page</Text>\n          </Link>\n          <Link to=\"/protected\" style={styles.navItem} underlayColor=\"#f0f4f7\">\n            <Text>Protected Page</Text>\n          </Link>\n        </View>\n\n        <Route path=\"/public\" component={Public} />\n        <Route path=\"/login\" component={Login} />\n        <PrivateRoute path=\"/protected\" component={Protected} />\n      </View>\n    </NativeRouter>\n  );\n}\n\nconst fakeAuth = {\n  isAuthenticated: false,\n  authenticate(cb) {\n    this.isAuthenticated = true;\n    setTimeout(cb, 100); // fake async\n  },\n  signout(cb) {\n    this.isAuthenticated = false;\n    setTimeout(cb, 100);\n  }\n};\n\nconst AuthButton = withRouter(({ history }) =>\n  fakeAuth.isAuthenticated ? (\n    <View>\n      <Text>Welcome!</Text>\n      <TouchableHighlight\n        style={styles.btn}\n        underlayColor=\"#f0f4f7\"\n        onPress={() => {\n          fakeAuth.signout(() => history.push(\"/\"));\n        }}\n      >\n        <Text>Sign out</Text>\n      </TouchableHighlight>\n    </View>\n  ) : (\n    <Text>You are not logged in.</Text>\n  )\n);\n\nfunction PrivateRoute({ component: Component, ...rest }) {\n  return (\n    <Route\n      {...rest}\n      render={props =>\n        fakeAuth.isAuthenticated ? (\n          <Component {...props} />\n        ) : (\n          <Redirect\n            to={{\n              pathname: \"/login\",\n              state: { from: props.location }\n            }}\n          />\n        )\n      }\n    />\n  );\n}\n\nfunction Public() {\n  return <Text style={styles.header}>Public</Text>;\n}\n\nfunction Protected() {\n  return <Text style={styles.header}>Protected</Text>;\n}\n\nclass Login extends Component {\n  state = { redirectToReferrer: false };\n\n  login = () => {\n    fakeAuth.authenticate(() => {\n      this.setState({ redirectToReferrer: true });\n    });\n  };\n\n  render() {\n    const { from } = this.props.location.state || { from: { pathname: \"/\" } };\n    const { redirectToReferrer } = this.state;\n\n    if (redirectToReferrer) {\n      return <Redirect to={from} />;\n    }\n\n    return (\n      <View>\n        <Text>You must log in to view the page at {from.pathname}</Text>\n\n        <TouchableHighlight\n          style={styles.btn}\n          underlayColor=\"#f0f4f7\"\n          onPress={this.login}\n        >\n          <Text>Log in</Text>\n        </TouchableHighlight>\n      </View>\n    );\n  }\n}\n\nconst styles = StyleSheet.create({\n  container: {\n    marginTop: 25,\n    padding: 10\n  },\n  header: {\n    fontSize: 20\n  },\n  nav: {\n    flexDirection: \"row\",\n    justifyContent: \"space-around\"\n  },\n  navItem: {\n    flex: 1,\n    alignItems: \"center\",\n    padding: 10\n  },\n  btn: {\n    width: 200,\n    backgroundColor: \"#E94949\",\n    justifyContent: \"center\",\n    alignItems: \"center\",\n    padding: 10,\n    marginTop: 10\n  }\n});\n\nexport default AuthExample;\n"